import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnExport, BtnView } from 'components/common/button';
import { Card, Row, Col, Table, Button } from 'antd';
import { TableData } from 'components/common';
import { getStudents } from 'services/tuititionReduction';
import { getQueryParams } from 'utils/urlUtil';
import FormSearchStudent from './FormSearchStudent';
import './style.scss';

class TuitionFeeSettingPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
    addingStudents: {},
    addingStudentsTableData: {},
    deletingStudents: {},
    selectedForRemoveRowKeys: []
    // deletingStudentsTableData: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onSelectForAddStudent = (selectedStudent, isSelect) => {
    const addingStudents = {...this.state.addingStudents};
    if(isSelect) {
      addingStudents[selectedStudent.studentId] = selectedStudent;
    } else {
      delete addingStudents[selectedStudent.studentId];
    }
    this.setState({ addingStudents });
  }

  onSelectAllForAddStudent = (selectedStudents, isSelect) => {
    if(isSelect) {
      const addingStudents = {};
      selectedStudents.forEach(student => {
        addingStudents[student.studentId] = student;
      });
      this.setState({ addingStudents });
    } else {
      this.setState({ addingStudents: {} });
    }
  }

  onSelectForRemoveStudent = (selectedStudent, isSelect) => {
    const deletingStudents = {...this.state.deletingStudents};
    console.log(selectedStudent);
    if(isSelect) {
      deletingStudents[selectedStudent.studentId] = selectedStudent;
    } else {
      delete deletingStudents[selectedStudent.studentId];
    }
    this.setState({
      deletingStudents,
      // selectedForRemoveRowKeys: Object.entries(deletingStudents).map(entry => entry[0])
    });
  }

  onSelectAllForRemoveStudent = (selectedStudents, isSelect) => {
    if(isSelect) {
      const deletingStudents = {};
      selectedStudents.forEach(student => {
        deletingStudents[student.studentId] = student;
      });
      this.setState({ deletingStudents });
    } else {
      this.setState({ deletingStudents: {} });
    }
  }

  onAddStudents = () => {
    const addingStudentsTableData = {...this.state.addingStudentsTableData};

    Object.entries(this.state.addingStudents).forEach(([key, value]) => {
      addingStudentsTableData[key] = value;
    });
    this.setState({ addingStudentsTableData });
  }

  onRemoveStudents = () => {
    const addingStudentsTableData = {...this.state.addingStudentsTableData}
    Object.entries(this.state.deletingStudents).forEach(studentEntry => {
      delete addingStudentsTableData[studentEntry[0]];
    });
    this.setState({ addingStudentsTableData, deletingStudents: {}, selectedForRemoveRowKeys: [] });
  }

  onViewRegistedCredits = () => {
    console.log("View Registed Credits");
  }

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  render() {
    const { reload, paramSearchClass, addingStudents, addingStudentsTableData, deletingStudents, selectedForRemoveRowKeys } = this.state;
    const isBtnAddVisible = addingStudents ? Object.entries(addingStudents).length < 1 : false;
    const isBtnDeleteVisible = deletingStudents ? Object.entries(deletingStudents).length < 1 : false;
    const isBtnViewVisible = addingStudents ? Object.entries(addingStudents).length !== 1 : false;
    const isBtnExportAndPrintDisable = addingStudentsTableData ? Object.entries(addingStudentsTableData).length < 1 : false;
    console.log(paramSearchClass);
    const columns = [
      {
        title: 'Mã SV',
        dataIndex: 'studentId',
        key: 'studentId',
        align: 'center'
      },
      {
        title: 'Họ và tên',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dateOfBirth',
        key: 'dateOfBirth',
        align: 'center'
      },
      {
        title: 'Giới tính',
        dataIndex: 'gender',
        key: 'gender',
        align: 'center'
      },
      {
        title: 'Tên lớp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'Đối tượng học phí',
        dataIndex: 'tuitionObject',
        key: 'tuitionObject',
      },

      {
        title: 'Số tín chỉ học đi',
        dataIndex: 'studyCredit',
        key: 'studyCredit',
        align: 'center'
      },
      {
        title: 'Số tín chỉ học lại',
        dataIndex: 'againCredit',
        key: 'againCredit',
        align: 'center'
      },
      {
        title: 'Số tín chỉ đã duyệt',
        dataIndex: 'approveCredit',
        key: 'approveCredit',
        align: 'center'
      },
      {
        title: 'Số tín chỉ chưa duyệt',
        dataIndex: 'unapproveCredit',
        key: 'unapproveCredit',
        align: 'center'
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        className: 'action-column',
        align: 'center',
        render: () => (
          <BtnView onClick={this.onViewRegistedCredits} />
        ),
      },
    ];

    const selectedStudentColumns = [
      {
        title: 'Mã SV',
        dataIndex: 'studentId',
        key: 'studentId',
        align: 'center'
      },
      {
        title: 'Họ và tên',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dateOfBirth',
        key: 'dateOfBirth',
        align: 'center'
      },
      {
        title: 'Giới tính',
        dataIndex: 'gender',
        key: 'gender',
        align: 'center'
      },
      {
        title: 'Tên lớp',
        dataIndex: 'class',
        key: 'class',
      },
      {
        title: 'đối tượng học phí',
        dataIndex: 'feeObjectType',
        key: 'feeObjectType',
      },
      {
        title: 'TC học đi',
        dataIndex: 'studyCredit',
        key: 'studyCredit',
      },
      {
        title: 'TC học lại',
        dataIndex: 'againCredit',
        key: 'againCredit',
      },
      {
        title: 'Số tiền phải nộp',
        dataIndex: 'requestedMoney',
        key: 'requestedMoney',
      },
      {
        title: '% MG',
        dataIndex: 'discountPercent',
        key: 'discountPercent',
      },
      {
        title: 'Số tiền miễn giảm',
        dataIndex: 'discountMoney',
        key: 'discountMoney',
      },
      {
        title: 'Số tiền còn nộp',
        dataIndex: 'remainMoney',
        key: 'remainMoney',
      },
    ];

    const rowSelection = {
      onSelect: (record, selected) => this.onSelectForAddStudent(record, selected),
      onSelectAll: (selected, selectedRows) => this.onSelectAllForAddStudent(selectedRows, selected)
    };

    const selectedTableRowSelection = {
      selectedRowKeys: selectedForRemoveRowKeys,
      onSelect: (record, selected) => this.onSelectForRemoveStudent(record, selected),
      onSelectAll: (selected, selectedRows) => this.onSelectAllForRemoveStudent(selectedRows, selected),
      onChange: selectedRowKeys => {
        this.setState({ selectedForRemoveRowKeys: selectedRowKeys });
      }
    }

    return (
      <Card title="DS lớp học">
        <Row>
          <Col span={24}>
            <FormSearchStudent isBtnViewVisible={isBtnViewVisible} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              // formSearch
              service={getStudents}
              paramSearch={paramSearchClass}
              columns={columns}
              rowSelection={rowSelection}
              rowKey="id"
            />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <div className="btn-group">
              <Button
                className="btnAction btnAdd"
                size="large"
                icon="plus-circle"
                onClick={this.onAddStudents}
                disabled={isBtnAddVisible}
              >
                Thêm SV miễn giảm
              </Button>
              <Button
                className="btnAction btnDelete"
                size="large"
                icon="delete"
                onClick={this.onRemoveStudents}
                disabled={isBtnDeleteVisible}
              >
                Xóa SV miễn giảm
              </Button>
              <BtnExport
                className="btnExport"
                title="Xuất Excel"
                disabled={isBtnExportAndPrintDisable}
              />
              <Button
                className="btnAction btnPrint"
                icon="printer"
                size="large"
                disabled={isBtnExportAndPrintDisable}
              >
                In
              </Button>
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              dataSource={Object.entries(addingStudentsTableData).map(entry => entry[1]) || []}
              columns={selectedStudentColumns}
              rowSelection={selectedTableRowSelection}
            />;
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TuitionFeeSettingPage />
  </Page>
);



