import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common';
import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import './style.scss';

@Form.create({ name: 'FormSearchClasses' })
class FormSearchStudent extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  getSubjectTypes = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'A',
        },
        {
          id: '2',
          label: 'B',
        },
        {
          id: '3',
          label: 'C',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 3,
    };
    const res = {
      data,
    };
    return res;
  };

  getBatches = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1',
        },
        {
          id: '2',
          label: '2',
        },
        {
          id: '3',
          label: '3',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 3,
    };
    const res = {
      data,
    };
    return res;
  };

  getReceipts = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'A',
        },
        {
          id: '2',
          label: 'B',
        },
        {
          id: '3',
          label: 'C',
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 3,
    };
    const res = {
      data,
    };
    return res;
  };

  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      updateQueryParams(values);
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const {
      form: { getFieldDecorator, getFieldValue, setFieldsValue },
    } = this.props;
    const yearEduId = getFieldValue('yearEduId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Năm học">
              {getFieldDecorator('yearEduId', {})(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('semesterId', {})(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Đợt">
              {getFieldDecorator('batch', {})(<SelectAjax service={this.getBatches} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Họ tên, Mã SV">
              {getFieldDecorator('studentNameCode')(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Khoản thu">
              {getFieldDecorator('receiptType', {})(<SelectAjax service={this.getReceipts} />)}
            </Form.Item>
          </Col>
          <Col xs={24} sm={24} md={24}>
            <div className="btn-group" style={{ float: 'right' }}>
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStudent;



